/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { useBlockProps, InspectorControls, BlockControls } from '@wordpress/block-editor';


/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';
import { SettingsProvider } from "./context/settings.context";
import { StylesProvider } from "./context/styles.context";
import { DetailProvider } from "./context/detail.context";
import BlockSettings from "./components/BlockSettings";
import BlockStyles from "./components/BlockStyles";
import Items from "./components/Items";

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {Element} Element to render.
 */
export default function Edit( props ) {
    const { attributes, setAttributes } = props;
    const { layout } = attributes.styles;
    const blockProps = useBlockProps( {
        className: 'sample-block-groups-blocks ' + (layout === 'grid' ? 'sample-block-groups-blocks--grid' : 'sample-block-groups-blocks--list')
    } );

    return (
        <SettingsProvider initialSettings={ attributes.settings } setAttributes={ setAttributes }>
            <StylesProvider initialStyles={ attributes.styles } setAttributes={ setAttributes }>
                <DetailProvider setAttributes={ setAttributes } attributes={ attributes }>
                    <div { ...blockProps }>
                        <InspectorControls>
                            <BlockSettings/>
                        </InspectorControls>
                        <BlockControls>
                            <BlockStyles/>
                        </BlockControls>
                        <Items/>
                    </div>
                </DetailProvider>
            </StylesProvider>
        </SettingsProvider>
    );
}